	module.exports = {"data":{"markdownRemark":{"html":"<h2>Product Owners</h2>\n<p>Product Owners lead the product vision and release planning. They are in charge of defining the features, the release date, and the content that makes up a shippable product. The goal of a Product Owner is to build the right thing quickly and correctly.</p>\n<p>At a high-level, the Product Owner is responsible for the following:</p>\n<ul>\n<li>Owner of the product vision</li>\n<li>Leads release planning</li>\n<li>Defines features and content of the release</li>\n<li>Manages team’s understanding of the release</li>\n<li>Creates and curates the product backlog</li>\n<li>Prioritizes the product backlog</li>\n<li>Guides team through the release cycle</li>\n<li>Makes trade-off decisions</li>\n<li>Accepts or rejections work</li>\n</ul>\n<p>The Product Owner creates a backlog of items that they think would make a good product. This knowledge is based on their understanding of the market, user testing, and market projection. Product Owners adjust the long-term goals of the product based on the feedback they receive from users and stakeholders. They also act as the point of contact with stakeholders and manage scope and expectations. </p>\n<p>Once the Product Owner has received feedback from the various stakeholders, they then refine the backlog to add as much detail as possible in order to create a Minimum Viable Product (MVP)for that release.</p>\n<p>The Product Owner then prioritizes the workload to ensure that the stories that are completed address both business value and user goals. Also, if there are risks associated with certain stories, the Product Owner puts those at the top of the backlog so that the risks are addressed early.</p>\n<p>Working with the team and the Scrum Master, the Product owner attends sprint planning meetings to loop groomed stories into the sprint. Throughout the sprint, the Product Owner ensures that the team completes the work according to the Definition of Done (DoD), answers any questions that may arise, and update stakeholders. </p>\n<p>When the sprint is complete, the Product Owner participates in the Sprint Review along with other stakeholders. Making sure that each story meets the DoD, the Product Owner prepares for the next sprint by gathering feedback and prioritizing work based on what was completed. </p>\n<h3>More Information:</h3>\n<ul>\n<li>Agile Product Ownership in a Nutshell video: <a href=\"https://www.youtube.com/watch?v=502ILHjX9EE\">YouTube</a></li>\n</ul>","fields":{"slug":"/agile/product-owners/"},"frontmatter":{"title":"Product Owners"}}},"pathContext":{"slug":"/agile/product-owners/"}}

